	</div>
</template>

<script>
import Fingerprint2 from 'fingerprintjs2'; // Introduce fingerprintjs2

export default {
    name: 'App',
    components: {},
    data() {
        return {
			fpComponents: []
		};

    },
    async created() {
        // You should not run fingerprints directly when or after the page loads. Instead, use setTimeout or requestIdleCallback to delay it for a few milliseconds to ensure consistent fingerprints.
        if (window.requestIdleCallback) {
            requestIdleCallback(() => {
                this.createFingerprint();
            });
        } else {
            setTimeout(() => {
                this.createFingerprint();
            }, 500);
        }
    },
    methods: {
        // Create browser fingerprint
        createFingerprint() {
            // Which information to choose as the basis for browser fingerprint generation
            const options = {
                // fonts: {
                //     extendedJsFonts: true,
                // },
                // excludes: {
                //     audio: true,
                //     userAgent: true,
                //     enumerateDevices: true,
                //     touchSupport: true,
                // },
            };
            // Browser fingerprint
            const fingerprint = Fingerprint2.get(options, (components) => {
                // When the parameter is only a callback function or options is {}, the default browser fingerprint is generated based on all configuration information
                const values = components.map((component) => component.value); // Array of configured values
                const murmur = Fingerprint2.x64hash128(values.join(''), 31); // Generate browser fingerprint
                console.log(components);
                console.log(values);
                console.log(murmur);
                localStorage.setItem('browserId', murmur); // Store the browser fingerprint, which is used in the project to verify user identity and bury points
				this.fpComponents = components
            });
        },
    },
};
</script>

